import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { CoverPagePropertiesComponent } from './cover-page-properties.component';

import {
  AutoFocusFirstInputModule,
  TabBarModule,
  FieldsModule,
  CheckboxModule,
  ModalHeaderModule,
  LabelModule,
  IconsModule,
} from 'shared-code-lib';
import { PageModule } from '../../../blocks/page/page.module';
import { TranslateModule } from '@ngx-translate/core';
import { ComponentsModule } from '../../../blocks/components/components.module';
import { FormsModule } from '@angular/forms';
import { PerfectScrollbarModule } from 'ngx-perfect-scrollbar-portable';
import { NgxSmartModalModule } from 'ngx-smart-modal';
import { RouterModule, Routes } from '@angular/router';

const routes: Routes = [
  {
    path: 'create',
    component: CoverPagePropertiesComponent,
    data: { type: 'create' },
    pathMatch: 'full',
  },

  {
    path: ':id',
    component: CoverPagePropertiesComponent,
    data: { type: 'properties' },
    pathMatch: 'full',
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class AdminCoverPagePropertiesRoutingModule {}

@NgModule({
  declarations: [CoverPagePropertiesComponent],
  imports: [
    CommonModule,
    TranslateModule,
    PageModule,
    ComponentsModule,
    FormsModule,
    PerfectScrollbarModule,
    IconsModule,
    FieldsModule,
    AutoFocusFirstInputModule,
    TabBarModule,
    CheckboxModule,
    NgxSmartModalModule,
    ModalHeaderModule,
    LabelModule,
    AdminCoverPagePropertiesRoutingModule,
  ],
})
export class AdminCoverPagePropertiesModule {}
