import { Component, ElementRef, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute, Data, Params, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { ToastrService } from 'ngx-toastr';
import { Subject, Subscription, combineLatest } from 'rxjs';
import {
  CoverPage,
  CoverPageBodyType,
  CoverPageType,
  DualPickListItem,
  FieldDefinition,
  FixedDataSource,
  ISearchItems,
  MetaDataType,
  RolePermissions,
  Strings,
  Substitution,
  Substitutions,
  Tab,
} from 'shared-code-lib';
import { FaxUserStatus, Group, IFaxUser } from '../../../blocks/InterFAX/Models/admin';
import { Log } from '../../../blocks/core/log';
import { ErrorsService } from '../../../blocks/services/errors.service';
import { InterFAXService } from '../../../blocks/services/interfax.service';
import { Control } from '../../../blocks/services/models';
import { SendFaxService } from '../../../blocks/services/send-fax.service';
import { SpinnerStateService } from '../../../blocks/services/spinner-state.service';
import { UserStateService } from '../../../blocks/services/user-state.service';

@Component({
  selector: 'app-cover-page-properties',
  templateUrl: './cover-page-properties.component.html',
  styleUrls: ['./cover-page-properties.component.scss'],
})
export class CoverPagePropertiesComponent implements OnInit, ISearchItems, OnDestroy {
  @ViewChild('spinnerWrapper', { static: true })
  spinnerWrapper: ElementRef;

  activeTab = 'body';
  tabs = new Array<Tab>();

  formControls: Array<Control> = [];
  saveButton: Control;
  cancelButton: Control;

  show: boolean;

  routeParams: Params;
  routeData: Data;

  fieldName: FieldDefinition;
  fieldType: FieldDefinition;
  fieldBodyType: FieldDefinition;
  fieldHeader: FieldDefinition;
  fieldHtmlBody: FieldDefinition;
  fieldFooter: FieldDefinition;

  coverPage: CoverPage;

  availableGroups: Array<Group> = [];
  currentGroups: Array<Group> = [];
  availableUsers: Array<DualPickListItem> = [];
  currentUsers: Array<DualPickListItem> = [];

  title = '';
  editMode: boolean;

  sendFaxProfileLoaded: boolean = false;
  hasTX: boolean = false;
  substitutions: Array<Substitution> = [];

  triggerRefocusSubject = new Subject<void>();
  triggerRefocusObservable = this.triggerRefocusSubject.asObservable();

  get coverPageType() {
    return `${this.coverPage.type}`;
  }
  set coverPageType(val: string) {
    this.coverPage.type = Number(val);
  }
  get coverPageBodyType() {
    return `${this.coverPage.coverpageBodyType}`;
  }
  set coverPageBodyType(val: string) {
    this.coverPage.coverpageBodyType = Number(val);
  }

  subscriptions = new Subscription();

  constructor(
    private translateService: TranslateService,
    private router: Router,
    private route: ActivatedRoute,
    private toastr: ToastrService,
    private translate: TranslateService,
    private spinner: SpinnerStateService,
    private interfaxService: InterFAXService,
    private userState: UserStateService,
    private errorsService: ErrorsService,
    private sendFaxService: SendFaxService
  ) {}

  ngOnInit(): void {
    this.createButtons();
    this.createFields();
    this.substitutions = new Substitutions(this.userState).getTemplatesSubstitutions();

    this.subscriptions.add(
      combineLatest([this.route.params, this.route.data]).subscribe(([params, data]) => {
        this.routeParams = params;
        this.routeData = data;
        this.load();
      })
    );

    this.hasTX = this.userState.hasRolePermission(RolePermissions.INTERFAX_SEND);
    this.sendFaxProfileLoaded = this.userState.userProfile?.fonts != null;
    if (this.hasTX && !this.sendFaxProfileLoaded)
      this.sendFaxService.loadSendFaxProfile(
        document.documentElement,
        this.onLoadingProfileSucceeded,
        this.onLoadingProfileFailed
      );
  }

  ngOnDestroy(): void {
    this.triggerRefocusSubject.complete();
    this.subscriptions.unsubscribe();
  }

  findMatches(searchTerm: string, completed: (items: Array<any>) => void): void {
    this.interfaxService.getFaxUsers(
      (items: Array<IFaxUser>) => {
        completed(items.map((u) => new DualPickListItem(u.userId, u.userId, u.iconUrl)));
      },
      () => {},
      searchTerm,
      FaxUserStatus.Open
    );
  }

  onLoadingProfileSucceeded = (): void => {
    this.spinner.hide();
    this.sendFaxProfileLoaded = true;
    this.triggerRefocusSubject.next();
  };

  onLoadingProfileFailed = (): void => {
    this.spinner.hide();
    this.errorsService.showErrorsModal(['SendFax.InterfaxAuthorizationProblem'], 'SendFax.HeadText');
  };

  private load = () => {
    if (this.routeData.type == 'create') {
      this.title = this.translateService.instant('cover-page.title-new');
      this.loaded(
        new CoverPage({
          id: 0,
          name: '',
          accountId: 0,
          htmlBody: '',
          binaryBody: [],
          footer: '',
          header: '',
          type: CoverPageType.Html,
          coverpageBodyType: CoverPageBodyType.Html,
          isPublic: true,
          users: [],
          groups: [],
        })
      );
    } else {
      this.editMode = true;
      this.title = this.translate.instant('cover-page.title-existing');
      this.spinner.show(this.translate.instant('Common.Loading'), document.documentElement);
      this.interfaxService.getCoverpage(this.routeParams.id, this.loaded, this.loadFailed);
    }
  };

  loaded = (coverPage: CoverPage): void => {
    this.spinner.hide();
    this.coverPage = coverPage;
    this.createTabs();
    this.spinner.show(this.translate.instant('cover-page.load-groups'), document.documentElement);
    this.interfaxService.getGroups(this.onGroupsLoaded, this.onGroupsLoadFailed);
    this.show = true;
  };

  loadFailed = (err: any): void => {
    this.spinner.hide();
    this.toastr.error(this.translate.instant('cover-page.load-cover-page-failed'));
    Log.error('cover-page.load-cover-page-failed', err);
  };

  onGroupsLoaded = (groups: Group[]): void => {
    this.availableGroups = groups.map((a: any) => {
      a.icon = 'fa-users';
      return a;
    });

    this.currentGroups = this.availableGroups.filter(
      (x) => this.coverPage.groups.find((id) => id.toString() == x.id) != null
    );

    this.spinner.hide();

    this.completeLoading();
  };

  onGroupsLoadFailed = (err: any): void => {
    this.spinner.hide();
    this.toastr.error(this.translate.instant('cover-page.load-groups-failed'));
    Log.error('cover-page.load-groups-failed', err);
    this.close();
  };

  completeLoading = (): void => {
    this.currentUsers = this.coverPage.users.map((u) => new DualPickListItem(u, u, ''));
    this.spinner.hide();
  };

  onUsersLoadFailed = (err: any): void => {
    this.spinner.hide();
    this.toastr.error(this.translate.instant('cover-page.load-users-failed'));
    Log.error('cover-page.load-users-failed', err);
  };

  createTabs = (): void => {
    this.tabs = [];

    if (this.coverPage.type == CoverPageType.Html) {
      this.tabs.push(new Tab('body', this.translateService.instant('cover-page.body')));
      this.tabs.push(new Tab('header', this.translateService.instant('cover-page.header')));
      this.tabs.push(new Tab('footer', this.translateService.instant('cover-page.footer')));
    }

    if (!this.coverPage.isPublic) {
      this.tabs.push(new Tab('groups', this.translateService.instant('cover-page.groups')));
      this.tabs.push(new Tab('users', this.translateService.instant('cover-page.users')));
    }

    if (this.tabs.findIndex((t) => t.id == this.activeTab) != -1) return;

    if (this.tabs.length == 0) {
      this.activeTab = '';
      return;
    }

    this.activateTab(this.tabs[0].id);
  };

  createFields = (): void => {
    this.fieldName = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translate.instant('cover-page.name'),
      placeholder: this.translate.instant('cover-page.name'),
      required: true,
    });

    this.fieldHeader = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translate.instant('cover-page.header'),
      placeholder: this.translate.instant('cover-page.header'),
    });

    this.fieldHtmlBody = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translate.instant('cover-page.body'),
      placeholder: this.translate.instant('cover-page.body'),
    });

    this.fieldFooter = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translate.instant('cover-page.footer'),
      placeholder: this.translate.instant('cover-page.footer'),
    });

    const typeOptions = [];
    Object.values(CoverPageType)
      .filter((x) => typeof x == 'number')
      .forEach((key) => {
        typeOptions.push({ name: this.translate.instant(`cover-page.types.${CoverPageType[key]}`), value: key });
      });

    const typeDataSource = new FixedDataSource(typeOptions);
    this.fieldType = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translate.instant('cover-page.type'),
      placeholder: this.translate.instant('cover-page.type'),
      datasource: typeDataSource,
    });

    const bodyTypeOptions = [];
    Object.values(CoverPageBodyType)
      .filter((x) => typeof x == 'number')
      .forEach((key) => {
        bodyTypeOptions.push({
          name: this.translate.instant(`cover-page.body-types.${CoverPageBodyType[key]}`),
          value: key,
        });
      });
    const bodyTypeDataSource = new FixedDataSource(bodyTypeOptions);
    this.fieldBodyType = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translate.instant('cover-page.body-type'),
      placeholder: this.translate.instant('cover-page.body-type'),
      datasource: bodyTypeDataSource,
    });
  };

  activateTab = (tab: string): void => {
    this.activeTab = tab;
  };

  private createButtons() {
    this.saveButton = new Control(this.onSave, 'Save', 'btn btn-primary btn-minwidth', '', '', 'btn', false);
    this.cancelButton = new Control(this.onCancel, 'Cancel', 'btn btn-light btn-minwidth', '', '', 'btn', false);
    this.formControls.push(this.saveButton);
    this.formControls.push(this.cancelButton);

    this.translateService.get('Common.Save').subscribe((res) => {
      this.saveButton.setTranslation(res);
    });
    this.translateService.get('Common.Cancel').subscribe((res) => {
      this.cancelButton.setTranslation(res);
    });
  }

  onHeaderChanged = (value: string): string => (this.coverPage.header = value);
  onHtmlBodyChanged = (value: string): string => (this.coverPage.htmlBody = value);
  onFooterChanged = (value: string): string => (this.coverPage.footer = value);

  groupsChanged = (current: Array<Group>): void => {
    this.coverPage.groups = current.map((x) => parseInt(x.id));
    this.currentGroups = current;
  };

  usersChanged = (current: Array<DualPickListItem>): void => {
    this.coverPage.users = current.map((x) => x.name);
    this.currentUsers = current;
  };

  validate(): boolean {
    const errors = [];

    if (Strings.isEmpty(this.coverPage.name)) errors.push('cover-page.name-required');

    if (this.coverPage.type == CoverPageType.Html) {
      if (Strings.isEmpty(this.coverPage.htmlBody)) errors.push('cover-page.body-required');

      if (
        this.coverPage.coverpageBodyType != CoverPageBodyType.None &&
        this.coverPage.htmlBody.toLocaleLowerCase().indexOf('%%fax.message%%') == -1
      )
        errors.push('cover-page.body-miss-message-var');
    }

    if (errors.length) {
      this.errorsService.showErrorsModal(errors, 'cover-page.save-fail');
      return;
    }

    return !errors.length;
  }

  onSave = () => {
    if (!this.validate()) return;

    this.spinner.show(this.translate.instant('Common.Saving'), document.documentElement);
    if (this.editMode) {
      this.interfaxService.updateCoverpage(this.coverPage, this.saved, this.saveFailed);
      return;
    }

    this.interfaxService.createCoverpage(this.coverPage, this.saved, this.saveFailed);
  };

  saved = (): void => {
    this.spinner.hide();
    this.toastr.success(
      this.translate.instant(this.editMode ? 'cover-page.save-success' : 'cover-page.create-success')
    );

    this.close();
  };

  saveFailed = (err: any): void => {
    this.spinner.hide();
    this.errorsService.showErrorsModal(err, 'cover-page.save-fail');
    Log.error('cover-page.load-cover-page-failed', err);
  };

  onCancel = () => {
    this.close();
  };

  close = () => {
    this.router.navigateByUrl(
      'product/settings/InterFAX/InterFAX-Upland.Bamba.InterFAX.Administration.CoverPages.Folder'
    );
  };
}
